<template lang="pug">
#headerNav
    .headerNav--logo
        h3 {{ logoTitle }}
    .headerNav--menu
        el-menu.el-menu-demo( theme="dark" v-bind:default-active="activeIndex" mode="horizontal" @select="handleSelect" )
            // 
            el-submenu( index="1" )
                template( slot="title" )
                    el-badge.item( v-bind:value="200" v-bind:max="99" )
                        el-button( size="small" )
                    i.material-icons email
                el-menu-item( index="1-1" ) A
                el-menu-item( index="1-2" ) B
            // 
            el-submenu( index="2" )
                template( slot="title" )
                    el-badge.item( v-bind:value="200" v-bind:max="99" )
                        el-button( size="small" )
                    i.material-icons notifications
                el-menu-item( index="2-1" ) A
                el-menu-item( index="2-2" ) B
                el-menu-item( index="2-3" ) C
            // 
            el-submenu( index="3" )
                template( slot="title" ) 
                el-menu-item( index="3-1" ) 
                el-menu-item( index="3-2" ) 
                el-menu-item( index="3-3" ) 
</template>

<script>
export default {
    data() {
        return {
            logoTitle: '',
            activeIndex: '1'
        }
    },
    methods: {
        handleSelect( key, keyPath ) {
            // console.log( key, keyPath )
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../../sass/main'

#headerNav
    position: fixed
    z-index: 2
    +W100
    .headerNav--logo
        +floatL
        +flexCenter
        +w-h( $D-sidebarWidth, $D-headerNavHeight )
        +bC( $C-theme )
        >h3
            +fS( $F-info )
            color: $F
    .headerNav--menu
        +mL( $D-sidebarWidth )
        min-width: 760px
        height: $D-headerNavHeight
        .el-menu
            display: flex
            justify-content: flex-end

//  - 
i.material-icons
    +fS( $F-title*1.5 )
    +pT( 5px )
</style>



