import Vue              from 'vue'
import Router           from 'vue-router'
import Login            from '@/views/Login'
import Home             from '@/views/Home'
// ''
import RentQuery                from '@/pages/Charge/RentManagement/RentQuery'                          //  - 
import RentRecord               from '@/pages/Charge/RentManagement/RentRecord'                         //  - 

import PropertyQuery            from '@/pages/Charge/PropertyCharges/PropertyQuery'                     //  - 
import PropertyRecord           from '@/pages/Charge/PropertyCharges/PropertyRecord'                    //  - 

import ElectricityBillQuery     from '@/pages/Charge/ElectricityBill/ElectricityBillQuery'              //  - 
import ElectricityBillRecord    from '@/pages/Charge/ElectricityBill/ElectricityBillRecord'             //  - 
import ElectricityBillEntry     from '@/pages/Charge/ElectricityBill/ElectricityBillEntry'              //  - 
import PeakflatvalleyEntry      from '@/pages/Charge/ElectricityBill/PeakflatvalleyEntry'               //  - 

import WaterFeeQuery            from '@/pages/Charge/WaterFee/WaterFeeQuery'                            //  - 
import WaterFeeRecord           from '@/pages/Charge/WaterFee/WaterFeeRecord'                           //  - 
import WaterFeeEntry            from '@/pages/Charge/WaterFee/WaterFeeEntry'                            //  - 

// 
import CustomerInformation      from '@/pages/Customer/CustomerInformation'                             // 
import ContractManagement       from '@/pages/Customer/ContractManagement'                              // 
import MarginManagement         from '@/pages/Customer/MarginManagement'                                // 

import AccessCardRegistration   from '@/pages/Customer/AccessCardManagement/AccessCardRegistration'     //  - 
import AccessCardDeposit        from '@/pages/Customer/AccessCardManagement/AccessCardDeposit'          //  - 

// 
import StatusDisplay            from '@/pages/Building/RoomQuery/StatusDisplay'                         //  - 
import RegistrationMessage      from '@/pages/Building/RoomQuery/RegistrationMessage'                   //  - 

import RoomManagement           from '@/pages/Building/RoomManagement/RoomManagement'                   //  - 
import BuildingManagement       from '@/pages/Building/RoomManagement/BuildingManagement'               //  - 

// 
import Rent                     from '@/pages/Financial/StatisticalReport/Rent'                         //  - 
import PropertyCosts            from '@/pages/Financial/StatisticalReport/PropertyCosts'                //  - 
import ElectricityBill          from '@/pages/Financial/StatisticalReport/ElectricityBill'              //  - 
import WaterFee                 from '@/pages/Financial/StatisticalReport/WaterFee'                     //  - 

import RentReview               from '@/pages/Financial/LeaseReview/RentReview'                         //  - 
import AuditRecord              from '@/pages/Financial/LeaseReview/AuditRecord'                        //  - 
import LeaseMarginReview        from '@/pages/Financial/LeaseReview/LeaseMarginReview'                  //  - 

import ElectricityAudit         from '@/pages/Financial/PropertyAudit/ElectricityAudit'                 //  - 
import WaterReview              from '@/pages/Financial/PropertyAudit/WaterReview'                      //  - 
import PropertyFeeAudit         from '@/pages/Financial/PropertyAudit/PropertyFeeAudit'                 //  - 
import PropertyMarginReview     from '@/pages/Financial/PropertyAudit/PropertyMarginReview'             //  - 

import ChargeSettings           from '@/pages/Financial/FinancialSettings/ChargeSettings'               //  - 
import BillingInformation       from '@/pages/Financial/FinancialSettings/BillingInformation'           //  - 

// 
import RoleManagement           from '@/pages/System/PermissionSettings/RoleManagement'                 //  - 
import DepartmentManagement     from '@/pages/System/PermissionSettings/DepartmentManagement'           //  - 

import DataDictionary           from '@/pages/System/DataDictionary'                                    // 

import OperationLog             from '@/pages/System/OperationLog'                                      // 


Vue.use( Router )

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Home',
            components: {
                default: Home
            },
            children: [
                {
                    path: '/',
                    components: {
                        ContainerView: RentQuery
                    }
                },
                // 
                {
                    path: '/RentQuery',
                    components: {
                        ContainerView: RentQuery
                    }
                }, {
                    path: '/RentRecord',
                    components: {
                        ContainerView: RentRecord
                    }
                }, {
                    path: '/PropertyQuery',
                    components: {
                        ContainerView: PropertyQuery
                    }
                }, {
                    path: '/PropertyRecord',
                    components: {
                        ContainerView: PropertyRecord
                    }
                }, {
                    path: '/ElectricityBillQuery',
                    components: {
                        ContainerView: ElectricityBillQuery
                    }
                }, {
                    path: '/ElectricityBillRecord',
                    components: {
                        ContainerView: ElectricityBillRecord
                    }
                }, {
                    path: '/ElectricityBillEntry',
                    components: {
                        ContainerView: ElectricityBillEntry
                    }
                }, {
                    path: '/PeakflatvalleyEntry',
                    components: {
                        ContainerView: PeakflatvalleyEntry
                    }
                }, {
                    path: '/WaterFeeQuery',
                    components: {
                        ContainerView: WaterFeeQuery
                    }
                }, {
                    path: '/WaterFeeRecord',
                    components: {
                        ContainerView: WaterFeeRecord
                    }
                }, {
                    path: '/WaterFeeEntry',
                    components: {
                        ContainerView: WaterFeeEntry
                    }
                }, 
                // 
                {
                    path: '/CustomerInformation',
                    components: {
                        ContainerView: CustomerInformation
                    }
                }, {
                    path: '/ContractManagement',
                    components: {
                        ContainerView: ContractManagement
                    }
                }, {
                    path: '/MarginManagement',
                    components: {
                        ContainerView: MarginManagement
                    }
                }, {
                    path: '/AccessCardRegistration',
                    components: {
                        ContainerView: AccessCardRegistration
                    }
                }, {
                    path: '/AccessCardDeposit',
                    components: {
                        ContainerView: AccessCardDeposit
                    }
                },
                // 
                {
                    path: '/StatusDisplay',
                    components: {
                        ContainerView: StatusDisplay
                    }
                }, {
                    path: '/RegistrationMessage',
                    components: {
                        ContainerView: RegistrationMessage
                    }
                }, {
                    path: '/RoomManagement',
                    components: {
                        ContainerView: RoomManagement
                    }
                }, {
                    path: '/BuildingManagement',
                    components: {
                        ContainerView: BuildingManagement
                    }
                },
                // 
                {
                    path: '/Rent',
                    components: {
                        ContainerView: Rent
                    }
                }, {
                    path: '/PropertyCosts',
                    components: {
                        ContainerView: PropertyCosts
                    }
                }, {
                    path: '/ElectricityBill',
                    components: {
                        ContainerView: ElectricityBill
                    }
                }, {
                    path: '/WaterFee',
                    components: {
                        ContainerView: WaterFee
                    }
                }, {
                    path: '/RentReview',
                    components: {
                        ContainerView: RentReview
                    }
                }, {
                    path: '/AuditRecord',
                    components: {
                        ContainerView: AuditRecord
                    }
                }, {
                    path: '/LeaseMarginReview',
                    components: {
                        ContainerView: LeaseMarginReview
                    }
                }, {
                    path: '/ElectricityAudit',
                    components: {
                        ContainerView: ElectricityAudit
                    }
                }, {
                    path: '/WaterReview',
                    components: {
                        ContainerView: WaterReview
                    }
                }, {
                    path: '/PropertyFeeAudit',
                    components: {
                        ContainerView: PropertyFeeAudit
                    }
                }, {
                    path: '/PropertyMarginReview',
                    components: {
                        ContainerView: PropertyMarginReview
                    }
                }, {
                    path: '/ChargeSettings',
                    components: {
                        ContainerView: ChargeSettings
                    }
                }, {
                    path: '/BillingInformation',
                    components: {
                        ContainerView: BillingInformation
                    }
                },
                // 
                {
                    path: '/RoleManagement',
                    components: {
                        ContainerView: RoleManagement
                    }
                }, {
                    path: '/DepartmentManagement',
                    components: {
                        ContainerView: DepartmentManagement
                    }
                }, {
                    path: '/DataDictionary',
                    components: {
                        ContainerView: DataDictionary
                    }
                }, {
                    path: '/OperationLog',
                    components: {
                        ContainerView: OperationLog
                    }
                }
            ]
        }
    ]
})



